document.getElementById('access').addEventListener('click', function(event) {
    event.preventDefault();
    getFieldsLogin();
});

function getForm(index) { return document.getElementsByTagName('form')[index] } 

function getFieldsLogin() {
    let fields = getForm(0).getElementsByTagName('input');
    for(var aux=0; aux<fields.length; aux++) validator(fields[aux].id, fields[aux].value);
}

function validator(id, value) {
    if(id === 'email') console.log(emailValidator(value));
    if(id === 'password') console.log(passwordValidator(value));

    // if(id === 'email') returnValidation(emailValidator(value), this.id);
    // if(id === 'password') returnValidation(passwordValidator(value), this.id);
} 

