function emailValidator(value) { return value.match(/^[a-zA-Z0-9][a-zA-Z0-9\._-]+@([a-zA-Z0-9\._-]+\.)[a-zA-Z-0-9]{2}/) }

function passwordValidator(value) { return value.match(/^[a-zA-Z0-9]/) }